import _ from 'underscore';
import Picker from 'vanilla-picker';
import html2canvas from 'html2canvas';

import './index.css';

import './components/LZWEncoder.js';
import './components/NeuQuant.js';
import './components/GIFEncoder.js';
import './components/b64.js';

var moveCursor = "data:image/x-icon;base64,";
var penCursor = "data:image/x-icon;base64,";
var _defaultImage = "data:image/png;base64,";

var _animationLayout = document.getElementById('animation');
var _animationLayoutFS =  document.getElementById('animation-full-screen');
var _frameTemplate = document.getElementById('frameTemplate').innerHTML;
var _defaultColor = "lightgray";
var _dragObjectId = null;
var _isLeftMouseDown = false;
var _isRightMouseDown = false;
var _currentAction = paintWithPen;
var _currrentElementList = [];

var _model = getDefaultModel();

function getDefaultModel(resolution) {
  var model = {};
  model.currentResolution = resolution ? resolution : 32;
  model.position = 1;
  model.primaryColor = "red";
  model.secondaryColor = "black";
  model.fps = 1;
  model.currentFrame = {
    image: _defaultImage,
    position: 1,
    id: "frame-" + uuidv4(),
    isSelected: true,
    matrix: getDefaultElementsMatrix(resolution)
  };
  model.frames = [model.currentFrame];
  _currrentElementList = flatten(model.currentFrame.matrix);
  return model;
}

function getDefaultElementsMatrix(resolution) {
  var matrix = [];
  for (var i = 0; i < resolution; i++) {
    matrix[i] = [];
    for (var j = 0; j < resolution; j++) {
      var id = "grid-cell-" + i + "-" + j;
      var element = { id: id, color: _defaultColor, isChanged: true, x: i, y: j };
      matrix[i][j] = element;
    }
  }
  return matrix;
}

function render(renderAll) {
  _currrentElementList.filter(x => x.isChanged || renderAll).forEach(function (cell) {
    var element = document.getElementById(cell.id);
    if (element) {
      element.style.backgroundColor = cell.color;
      cell.isChanged = false;
    }
  });

  _model.currentFrame.isChanged = true;

  document.getElementById('primaryColorBtnInd').style.backgroundColor = _model.primaryColor;
  document.getElementById('secondaryColorBtnInd').style.backgroundColor = _model.secondaryColor;
}

function paintWithPen(targetElement) {
  if (_isLeftMouseDown || _isRightMouseDown) {
    var element = _currrentElementList.find(x => x.id == targetElement.id);
    element.isChanged = true;
    element.color = _isLeftMouseDown ? _model.primaryColor : _model.secondaryColor;
  }
};

function erase(targetElement) {
  if (_isLeftMouseDown || _isRightMouseDown) {
    var element = _currrentElementList.find(x => x.id == targetElement.id);
    if (element) {
      for (var i = element.x - 1; i <= element.x + 1; i++) {
        for (var j = element.y - 1; j <= element.y + 1; j++) {
          var row = _model.currentFrame.matrix[i];
          if (row) {
            var cell = row[j];
            if (cell) {
              cell.color = _defaultColor;
              cell.isChanged = true;
            }
          };
        }
      }
    }
  }
};

function paintBucket(targetElement) {
  var element = _currrentElementList.find(x => x.id == targetElement.id);
  var oldColor = element.color;
  var newColor = _isLeftMouseDown ? _model.primaryColor : _model.secondaryColor;
  if (oldColor == newColor) return;
  paintBucketRecursion(element.x, element.y, newColor, oldColor);
  render();
};

function paintBucketRecursion(x, y, newColor, oldColor) {
  if (x >= 0 && y >= 0 && x < _model.currentResolution && y < _model.currentResolution && _model.currentFrame.matrix[x][y].color == oldColor) {
    _model.currentFrame.matrix[x][y].color = newColor;
    _model.currentFrame.matrix[x][y].isChanged = true;
    paintBucketRecursion(x - 1, y, newColor, oldColor);
    paintBucketRecursion(x, y - 1, newColor, oldColor);
    paintBucketRecursion(x + 1, y, newColor, oldColor);
    paintBucketRecursion(x, y + 1, newColor, oldColor);
  }
}

function chooseColor(targetElement) {
  var element = _currrentElementList.find(x => x.id == targetElement.id);
  _model.primaryColor = element.color;
};

function renderFrames() {
  _model.frames.forEach((x) => {

    var element = document.getElementById(x.id);

    if (!element) {
      var element = document.createElement("li");
      element.innerHTML = _frameTemplate
        .replace("#id#", x.id)
        .replace("#background-image#", x.image)
        .replace("#position#", x.position);
      document.getElementById("frames").appendChild(element);

      element.addEventListener('click', function () { selectFrame(x.id) });
    } else {
      element.parentElement.innerHTML = _frameTemplate
        .replace("#id#", x.id)
        .replace("#background-image#", x.image)
        .replace("#position#", x.position);
    }

    var element = document.getElementById(x.id);

    if (x.isSelected) {
      element.setAttribute("class", "frame frame-selected");
    } else {
      element.setAttribute("class", "frame");
    }
  });
}

function move() { }

function saveState() {
  var savedInfo = {
    "model": _model
  }
  localStorage.setItem('savedInfo', JSON.stringify(savedInfo));
}

function initModel() {
  var savedInfo = JSON.parse(localStorage.getItem('savedInfo'));
  if (savedInfo) {
    _model = savedInfo.model;
  } else {
    _model = getDefaultModel(32);
  }
  _currrentElementList = flatten(_model.currentFrame.matrix);
}

function setupPalette() {
  var palette = document.getElementById("palette");
  palette.innerHTML = '';
  palette.setAttribute("style", "grid-template-rows: repeat(" + _model.currentResolution + ",1fr); grid-template-columns: repeat(" + _model.currentResolution + ",1fr)");
  for (var i = 0; i < _model.currentResolution; i++) {
    for (var j = 0; j < _model.currentResolution; j++) {
      var id = "grid-cell-" + i + "-" + j;
      var element = document.createElement('div');
      element.setAttribute("id", id);
      element.setAttribute("class", "canvas-grid-item");
      palette.appendChild(element);
    }
  }
  render();
}

function initEventsAndUi() {

  setupPalette();

  var modal = document.getElementById('myModal');

  _animationLayout.onclick = function () {
    modal.style.display = "block";
  };

  var animationConteiner = document.getElementsByClassName("close")[0];

  animationConteiner.onclick = function () {
    modal.style.display = "none";
  };

  document.getElementById('resolutionSelection').value = _model.currentResolution;

  document.getElementById('paintActionBtn').onclick = function () {
    document.body.style.cursor = "url('" + penCursor + "'), auto";
    _currentAction = paintWithPen;
  };

  document.getElementById('eraseActionBtn').onclick = function () {
    _currentAction = erase;
  };

  document.getElementById('paintBucketActionBtn').onclick = function () {
    _currentAction = paintBucket;
  };

  document.getElementById('chooseColorActionBtn').onclick = function () {
    _currentAction = chooseColor;
  };

  document.getElementById('moveActionBtn').onclick = function () {
    document.body.style.cursor = "url(" + moveCursor + "), auto";
    _currentAction = move;
  };

  new Picker({
    parent: document.getElementById('primaryColorBtnInd'),
    popup: 'top',
    editorFormat: 'hex',
    onDone: function (color) {
      _model.primaryColor = color.hex;
      render();
    },
  });

  new Picker({
    parent: document.getElementById('secondaryColorBtnInd'),
    popup: 'top',
    editorFormat: 'hex',
    onDone: function (color) {
      _model.secondaryColor = color.hex;
      render();
    },
  });

  document.getElementById('palette').onmouseover = function (arg) {
    if (!_isLeftMouseDown && !_isRightMouseDown) return;

    var target;

    if (arg && arg.target) {

      target = arg.target;

      if (target.id.includes("grid-cell") && (_currentAction == paintWithPen || _currentAction == erase)) {

        _currentAction(target);
      }
    }
    _.throttle(render, 50)();
  };

  document.getElementById('palette').onmouseup = _.debounce(function () {
    saveState();
    getImage();
  }, 1000);

  document.getElementById("resolutionSelection").onchange = function () {
    localStorage.clear();
    document.getElementById('frames').innerHTML = '';
    document.getElementById('palette').innerHTML = '';
    _model = getDefaultModel(+document.getElementById('resolutionSelection').value);
    setupPalette();
    renderFrames();
    render();
  }

  window.addEventListener('mousedown', function (e) {
    if (e.button == 2) {
      _isRightMouseDown = true;
    } else {
      _isLeftMouseDown = true;
    }
  });

  window.addEventListener('mouseup', function (e) {
    _isLeftMouseDown = false;
    _isRightMouseDown = false;
  });

  window.addEventListener("mousedown", function (arg) {
    var target;

    if (arg && arg.target) {

      target = arg.target;

      if (target.id.includes("grid-cell")) {
        _currentAction(target);
      }
    }
    render();
  });

  function onMouseDown(event) {

    var temp = document.elementFromPoint(event.clientX, event.clientY);
    if (_currentAction == move && temp && temp.parentElement.classList.contains("frame")) {
      _dragObjectId = temp.parentElement.id;
    }
  }

  function onMouseUp(event) {

    var dropObject = document.elementFromPoint(event.clientX, event.clientY);

    if (_currentAction == move && dropObject.parentElement.classList.contains("frame") && _dragObjectId) {

      var dragElement = _model.frames.find(x => x.id == _dragObjectId);
      var dropElement = _model.frames.find(x => x.id == dropObject.parentElement.id);


      if (dragElement && dragElement) {

        var tempPosition = dragElement.position;
        dragElement.position = dropElement.position;
        dropElement.position = tempPosition;

        _model.frames.sort((a, b) => a.position - b.position);
        document.getElementById("frames").innerHTML = "";

        renderFrames();
      }
    } else {
      _dragObjectId = null;
    }
  }

  document.getElementById('frames').onmousedown = onMouseDown;
  document.getElementById('frames').onmouseup = onMouseUp;

  var fpsIndicator = document.getElementById('fps-value');
  fpsIndicator.innerText = "12 FPS";

  document.getElementById('fps').onchange = function (e) {
    _model.fps = Math.round(1 + 23 / 100 * e.target.value);
    fpsIndicator.innerText = _model.fps + " FPS";
  }

  renderFrames();
};

function createFrame() {
  _model.frames.push({
    image: _defaultImage,
    position: _model.frames.length + 1,
    id: "frame-" + uuidv4(),
    isSelected: false,
    matrix: getDefaultElementsMatrix(_model.currentResolution),
  });
  renderFrames();
  saveState();
}

function deleteFrame(e) {
  if (_model.frames.length == 1) {
    _model.frames[0].matrix = getDefaultElementsMatrix(_model.currentResolution);
    _currrentElementList = flatten(_model.frames[0].matrix);
    _model.frames[0].isSelected = true;
    _model.frames[0].image = _defaultImage;
    _model.currentFrame = _model.frames[0];
    render(true);
    renderFrames();
    return;
  }

  var elementData = _model.frames.find(x => x.id == e.parentElement.attributes["id"].value);

  _model.frames.filter(x => x.position > elementData.position).forEach((x) => {
    x.position--;
  })

  _model.frames = _model.frames.filter(x => x.id !== elementData.id);

  e.parentElement.remove();

  renderFrames();
  saveState();
}

function copyFrame(e) {

  var element = _model.frames.find(x => x.id == e.parentElement.attributes["id"].value);

  _model.frames.filter(x => x.position > element.position).forEach((x) => {
    x.position++;
  })

  _model.frames.push({
    image: element.image,
    position: element.position + 1,
    id: "frame-" + uuidv4(),
    isSelected: false,
    matrix: JSON.parse(JSON.stringify(element.matrix))
  });

  _model.frames.sort((a, b) => a.position - b.position);

  document.getElementById("frames").innerHTML = "";

  renderFrames();
  saveState();
}

function selectFrame(frameId) {
  _model.frames.forEach(x => x.isSelected = false);
  var selectedFrame = _model.frames.find(x => x.id == frameId);
  if (selectedFrame) {
    _model.currentFrame = selectedFrame;
  } else {
    _model.currentFrame = _model.frames[0];
  }
  _currrentElementList = flatten(_model.currentFrame.matrix);
  _model.currentFrame.isSelected = true;
  renderFrames();
  render(true);
  saveState();
}

function flatten(arr) {
  return arr.reduce(function (flat, toFlatten) {
    return flat.concat(Array.isArray(toFlatten) ? flatten(toFlatten) : toFlatten);
  }, []);
}

function uuidv4() {
  return ([1e7] + -1e3 + -4e3 + -8e3 + -1e11).replace(/[018]/g, c =>
    (c ^ crypto.getRandomValues(new Uint8Array(1))[0] & 15 >> c / 4).toString(16)
  );
}

function getImage(frame) {
  html2canvas(document.getElementById("palette-wrapper"))
    .then(function (canvas) {
      var resizedCanvas = document.createElement("canvas");
      var resizedContext = resizedCanvas.getContext("2d");
      resizedCanvas.height = "200";
      resizedCanvas.width = "200";
      resizedContext.drawImage(canvas, 0, 0, 200, 200);
      _model.currentFrame.image = resizedCanvas.toDataURL();
    });
}

function download() {

  var encoder = new GIFEncoder();
  encoder.setRepeat(0);
  encoder.setDelay(1000 / _model.fps);
  encoder.setSize(200, 200);

  encoder.start();

  _model.frames.forEach((x) => {
    var canvas = document.createElement("canvas");
    var ctx = canvas.getContext("2d");
    canvas.height = "200";
    canvas.width = "200";
    var image = new Image();
    image.src = x.image;
    ctx.drawImage(image, 0, 0, 200, 200);
    encoder.addFrame(ctx);
  })

  encoder.finish();
  encoder.download("piskel.gif");
}

window.createFrame = createFrame;
window.deleteFrame = deleteFrame;
window.copyFrame = copyFrame;
window.selectFrame = selectFrame;
window.download = download;

function runAnimation() {
  var frame = _model.frames.find(x => x.position == _model.position);
  var delay = 1000 / _model.fps;

  if (frame) {
    setTimeout(function () {
      _animationLayout.setAttribute("style", "background-image: url(" + frame.image + "); background-repeat: no-repeat");
      _animationLayoutFS.setAttribute("style", "background-image: url(" + frame.image + "); background-repeat: no-repeat; background-position: center; background-size: cover;");
      _model.position = _model.position < _model.frames.length ? _model.position + 1 : 1;
      renderFrames();
      runAnimation();
    }, delay);
  } else {
    _model.position = 1;
  }
}

initModel();
initEventsAndUi();
render(true);
runAnimation();
